export default {
	data() {
		return {
			titleArr: [
			  {
			    name: '图片介绍',
			    boxId: 'lineIntro',
			    boxTop: 0,
			    isActive: true
			  },
			  {
			    name: '行程线路',
			    boxId: 'journeyLine',
			    boxTop: 0,
			    isActive: false
			  },
			  {
			    name: '费用说明',
			    boxId: 'useExplain',
			    boxTop: 0,
			    isActive: false
			  },
			  {
			    name: '购买须知',
			    boxId: 'buyNotice',
			    boxTop: 0,
			    isActive: false
			  }
			],
			
			lineArr: [{
				title: "长沙到张家界",
				desc: "这里是测试文本这里是测试文本这里是测试文本这里是测试文本",
				childList: [{
				  name: "7:00 酒店(集合点)",
				  type: 2, //1、吃 2/住 3/行 4/景点
				  desc: "卡尼亚酒店和醋大煞风景可能都是艰苦奋斗精神发布活动是暴发户似的不符合基本都是粉白黛黑上发布活动时不妨喝点水别废话都是不符合时代步伐会不定时发布黄色的暴发户的不是。",
				  imgList: ['/static/test_img_2.jpeg', '/static/test_img_3.jpeg']
				}, {
				  name: "16:00 张家界天门山",
				  type: 3, //1、吃 2/住 3/行 4/景点
				  desc: "卡尼亚酒店和醋大煞风景可能都是艰苦奋斗精神发布活动是暴发户似的不符合基本都是粉白黛黑上发布活动时不妨喝点水别废话都是不符合时代步伐会不定时发布黄色的暴发户的不是",
				  imgList: ['/static/test_img_4.jpeg']
				}]   
			}, {
				title: "长沙到张家界",
				desc: "这里是测试文本这里是测试文本这里是测试文本这里是测试文本",
				childList: [{
				  name: "7:00 酒店(集合点)",
				  type: 2, //1、吃 2/住 3/行 4/景点
				  desc: "卡尼亚酒店和醋大煞风景可能都是艰苦奋斗精神发布活动是暴发户似的不符合基本都是粉白黛黑上发布活动时不妨喝点水别废话都是不符合时代步伐会不定时发布黄色的暴发户的不是。",
				  imgList: ['/static/test_img_2.jpeg', '/static/test_img_3.jpeg']
				}, {
				  name: "16:00 张家界天门山",
				  type: 3, //1、吃 2/住 3/行 4/景点
				  desc: "卡尼亚酒店和醋大煞风景可能都是艰苦奋斗精神发布活动是暴发户似的不符合基本都是粉白黛黑上发布活动时不妨喝点水别废话都是不符合时代步伐会不定时发布黄色的暴发户的不是",
				  imgList: ['/static/test_img_4.jpeg']
				}]   
			}],
			
			navCutTop: null,
			navPostion: 'absolute',
			isFloat: false, //导航浮动
			scrollNum: null,
			isActive: false
		}
	},
	props: {
		_scrollTop: {
			type: Number,
			default: 0
		}
	},
	computed: {
		customBar() {
			return this.CustomBar + 'px'
		}
	},
	watch: {
		_scrollTop(val) {
			if (!this.navCutTop) {
			  this.queryMultipleNodes('#contentNavBox', (top) => {
				this.navCutTop = top
			  })
			}else {
			  if (val > this.navCutTop) {
			    if (!this.isFloat) {
				  this.navPostion = 'fixed'
				  this.isFloat = true
			    }
			  } else {
			    if (this.isFloat) {
				  this.navPostion = 'absolute'
				  this.isFloat = false
			    }
			  }
			}
			
			let arr = this.titleArr
			for(let i = 0; i<arr.length; i++) {
			  if(!arr[i].boxTop) {
			    this.queryMultipleNodes(`#${arr[i].boxId}`, (top) => {
					// arr[i].boxTop = top - (wx.getSystemInfoSync().windowWidth / 750) * 10
					arr[i].boxTop = top - this.CustomBar
			    })
			  }else {
			    if (val > arr[0].boxTop) {
			      if (arr[i + 1]) {
			        if (arr[i].boxTop <= val && val < arr[i + 1].boxTop) {
			          this.scrollListen(i, n => {
			              this.titleArr.map((v, ind) => {
			                if(ind == n) {
							  this.titleArr[ind].isActive = true
			                }else {
							  this.titleArr[ind].isActive = false
			                }
			              })
			          })
			        }
			      }else {
			        if (val > arr[i].boxTop) {
			          this.scrollListen(i, n => {
			            this.titleArr.map((v, ind) => {
			              if (ind == n) {
							this.titleArr[ind].isActive = true
			              } else {
			                this.titleArr[ind].isActive = false
			              }
			            })
			          })
			        }
			      }
			    }else {
				  this.titleArr[0].isActive = true
			    }
			  }
			}
		}
	},
	methods: {
		scrollListen (n, callback) {
		  if (this.scrollNum === null) this.scrollNum = n
		
		  if (this.scrollNum == n) {
		    if (!this.isActive) {
		      this.isActive = true
		      typeof callback == 'function' && callback(n)
		    }
		  } else {
		    // this.isActive = false
		    this.scrollNum = n
			typeof callback == 'function' && callback(n)
		  }
		},
		queryMultipleNodes(id,callBack) {
			var query = uni.createSelectorQuery().in(this)//创建节点查询器 query
			query.select(id).boundingClientRect()//这段代码的意思是选择Id=the-id的节点，获取节点位置信息的查询请求
			query.selectViewport().scrollOffset()//这段代码的意思是获取页面滑动位置的查询请求
			query.exec(function (res) {
			  typeof callBack == 'function' && callBack(res[0].top)
			})
		},
		toCutTitle(idx) {
			this.titleArr.map((v, i) => {
				if(idx == i) {
					v.isActive = true
				}else {
					v.isActive = false
				}
			})
		}
	}
}
